/* eslint-disable */
import '../App.css';
import { Figure } from 'react-bootstrap';
import developer from '../images/developer.png';

function About() {
    return (
        <div id="about" className="container">
            <p className="title">ABOUT</p>
            <div className="row">
                <div className="col-md-6">
                    <Figure>
                        <Figure.Image
                            width={350}
                            height={250}
                            alt="developer"
                            src={developer}
                        />
                    </Figure>
                </div>
                <div className="col-md-6">
                    <p className="subtitle">신입<span>이 되고 싶은</span>개발자</p>
                    <p> 안녕하세요. 저는 프론트에 입문한지 이제 막 6개월 된 병아리 개발자입니다.
                        개발에 흥미를 느껴 컴퓨터 공학을 이중전공 하였고, 무엇이든 제대로 알고 쓰려고 노력하고 있습니다.
                        이 페이지는 저의 포트폴리오 홈페이지로 React와 Javascript를 사용하여 구현했습니다.
                    </p>
                </div>
            </div>
        </div>
    )
}

export default About;