import '../App.css';
import { Image } from 'react-bootstrap';
import html5 from '../images/html5.png';
import css3 from '../images/css3.png';
import js from '../images/js.png';
import react from '../images/react.png';
import php from '../images/php.png';
import oracle from '../images/oracle.png';
import mysql from '../images/mysql.png';

function Skills() {
    return (
        <div id="skills">
            <p className="title">SKILLS</p>

            <table>
                <tbody>
                    <tr>
                        <td>
                            <Image className="skill-img" src={html5} rounded />
                            <Image className="skill-img" src={css3} rounded />
                            <Image className="skill-img" src={js} rounded />
                            <Image className="skill-img" src={react} rounded />
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <Image className="skill-img" src={php} rounded />
                            <Image className="skill-img" src={oracle} rounded />
                            <Image className="skill-img" src={mysql} rounded />
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    )
}

export default Skills;