import { useState } from 'react';
import '../App.css';
import { Button } from 'react-bootstrap';
import shoppingmall from '../images/shoppingmall.png';
import board from '../images/board.png';
import pos from '../images/pos.png';
import supermario from '../images/supermario.jpg';
import metro from '../images/metro.png';

function Projects() {

    let [project, setProject] = useState(
        [{title : '게시판', skill : 'HTML5 / CSS3 / PHP', img : board, href : "https://github.com/youngjuu/board"},
         {title : 'PC방 POS시스템', skill : 'Python / PyQt', img : pos, href : "https://github.com/youngjuu/PC-room-POS-system"},
         {title : '슈퍼마리오 게임', skill : 'JAVA / GIMP', img : supermario, href : "https://github.com/youngjuu/supermario-game"}, 
         {title : '쇼핑몰', skill : 'React / HTML5 / CSS3 (테스트중...)', img : shoppingmall, href : null}, 
         {title : 'SITWAY', skill : '준비중...', img : metro, href : null}
        ]
    );

    return (
        <div id="projects">
            <p className="title">PROJECTS</p>

            <div className="flex-box">
            {
                project.map((a, i)=>{
                    return <Card project={a} key={i}/>
                })
            }
            </div>
            
            
        </div>
    )
}

function Card(props) {
    return (
        <div className="project-card">
            <img className="project-img" src={props.project.img} alt={props.project.title} />
            <div className="text-area">
                <h4>{props.project.title}</h4>
                <p>{props.project.skill}</p>
                {
                    props.project.href != null
                    ? (
                        <a href={props.project.href} target="_blank">
                            <p><Button className="detail-button" variant="info">자세히 보기</Button></p>
                        </a>
                    )
                    : null
                }
            </div>
        </div>
    )
}

export default Projects;